import YCPDpng from './../../assets/YCPD.png';
import VerifiCodePng from './../../assets/Verification-Code.png';
import VerifiTipePng from './../../assets/Verification-Tip.png';

import GVerify from './../../components/GVerify.js';
import loadPageVar from './../../utils/loadPageVar';
import request from './../../utils/request';
import ajaxs from './ajaxs.js';

import { Indicator } from 'mint-ui';
import 'mint-ui/lib/style.min.css';

export default {
  name: 'security',

  data () {
    return {
      picture: { // 图片
        YCPDpng: YCPDpng,             // 养车频道的logo图
        VerifiCodePng: VerifiCodePng, // 验证码
        VerifiTipePng: VerifiTipePng, // 16位数字防伪码 (暂时)
      },

      urlParam: '',              // URL 解析 数据

      securityCode: '',          // 防伪码
      securityCodeError: '',     // 防伪码错误信息
      
      verificationCode: '',      // 验证码
      verificationCodeError: '', // 验证码错误信息
      
      verifyCodeComponent: null,          // 验证码的组件

      isLoadByqrCode: false,  // 是否是通过二维码扫描, 如果是 则不显示 请输入防伪码 部分
      isLoadedProduct: false, // 是否已经查询商品
      loadedErrorMsg: '',     // 查询失败时候显示错误信息

      qrCode: {
        UnitCode: 'code', // ae6e4931-2d0f-4453-9cd5-09be5fd5e3d8
        Name: '品名', // 品名
        StoreName: '门店名称', // 门店名称
        SupplyDate: '门店时间', // 门店时间
        StringVal2: '半合成', // 半合成
        IntVal9: 4, // 包装1
        IntVal10: 4, // 包装2
        StringVal4: '规格', // 包装2 规格
        ProductionAddress: '测试产地', // 产地
        Manufacturer: '测试制造商',      // 制造商
        DeclaringCustomsID: '51662016666201885', // 报关单
        DeclaringCustomsImg: 'img.jpg',
        QualityTesting: 'PDS Castrol Magnatec 5W-30 C3', // 质检报告单
        QualityTestingImg: 'img.jpg',
        ProductData: 'Magnatec 5W-30 C3', // 产品数据表
        ProductDataImg: 'img.jpg',
        ProductID: '170902010000013090'
      }
    }
  },

  methods: {
    verifyInput: function () { // 输入验证
      if (this.securityCode === '') {
        this.securityCodeError = '防伪码不能为空';
        return request.error('防伪码不能为空!');
      }

      if (this.verifyCodeComponent.validate(this.verificationCode) === false) {
        this.verificationCodeError = '请输入正确的验证码';
        return request.error('请输入正确的验证码!');
      }

      return request.success();
    },

    securitySubmit: function () { // 查询提交
      let _this = this;
      let verifySubmit = this.verifyInput();

      const solveQRcodeData = qrCodeData => { // 数据转换
        _this.qrCode = qrCodeData[0];
      }

      if (verifySubmit.result !== 1) { // 验证失败
        alert(verifySubmit.message);

      } else {
        Indicator.open('正在查询...');

        ajaxs.GetBarcodeDetail(this.securityCode)
        .then(val => {
          solveQRcodeData(val);          // 数据处理
          _this.verifyCodeComponent.refresh(); // 刷新二维码
          _this.isLoadedProduct = true;
          Indicator.close();
        }, error => {
          _this.isLoadedProduct = true;
          _this.loadedErrorMsg = error;
          Indicator.close();
          alert(error);
        });
      }
    },

    initGVerify: function () { // 初始化二维码
      this.verifyCodeComponent = new GVerify({
        id: "gVerifyContainer",
        width: "150",
        height: "42",
      });
    },

    initUrlParam: function () { // 初始化 参数
      const _this = this;

      this.urlParam = loadPageVar('id');

      const solveQRcodeData = qrCodeData => { // 数据转换
        _this.securityCode = qrCodeData[0].UnitCode;
        _this.qrCode = qrCodeData[0];
      }

      if (this.urlParam) { // 如果有参数, 则开始查询!
        this.isLoadByqrCode = true; // 说明是 扫码进入
        Indicator.open('正在查询...');

        ajaxs.getQRcodeBy(this.urlParam)
        .then(val => {
          solveQRcodeData(val); // 数据处理
          _this.isLoadedProduct = true; // 直接展示数据
          Indicator.close();
        }, error => {
          _this.securityCode = _this.urlParam;
          _this.isLoadedProduct = true;
          _this.loadedErrorMsg = error;
          Indicator.close();
          alert(error);
        });

        return true
      } else {
        this.isLoadByqrCode = false; // 说明不是 扫码进入
        return false
      }
    }
  },

  watch: {
    securityCode: function (newSecurityCode, oldSecurityCode) {
      this.securityCodeError = ''; // 清空错误信息
    },

    verificationCode: function (newVerificationCode, oldVerificationCode) {
      this.verificationCodeError = ''; // 清空错误信息
    },
  },

  mounted: function() {
    if (this.initUrlParam() === false) { // 初始化 扫描二维码传入的参数
      this.initGVerify(); // 如果没有传入扫描二维码的参数, 则初始化验证码
    }
  }
}




